import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';

import CountDown from '../components/CountDown';
import PeriodForm from '../components/PeriodForm';
import ShowNotification from '../components/ShowNotification';
import DataTable from '../components/DataTable';

import getCookie from '../utils/getCookie';

export default class Period extends Component {
	state = {period: {} }
  dataTable = React.createRef();

  constructor(props) {
    super(props);
    var self = this;
    window.periodListActionEdit = this.editPeriod;

    this.dataTableConfig = {
      order: [[0, 'desc']],
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        axios.get('/api/setup/periods', {params: {params}}).then(response=>{
          callback(response.data);
        })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: 'created_at', name: 'started', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
        { data: 'end', name: 'ended', 'class': 'text-center', render: (data, type, row) => data ? (new Date(data)).toDateString() : 'Still running'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="periodListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
          </span>`
        }}
      ],
      rowCallback: function( row, data ) {
        let jRow = $(row);
        jRow.tooltip('dispose');
        if(self.state.period && self.state.period.id == data.id) {
          jRow.tooltip({toggle: "tooltip", placement: "bottom", title: 'Active' });
          row.classList.add('table-success');
        } else {
          row.classList.remove('table-success');
        }
      }
    };
  }

	componentDidMount() {
    axios.get('/api/setup/periods/current').then(res=>{
      this.setState({period: res.data});
      this.dataTable.current.table.draw();
    });
  }

  componentWillUnmount() {
    $('tr[title]').tooltip('dispose');
  }

  editPeriod = (period) => {
  	this.props.history.push(`${this.props.match.url}/${period}`);
  }

  closePeriod = () => {
  	axios.post('/api/setup/periods/close', {id: this.state.period.id}).then(res => {
  		this.setState({ period: '' });
  	})
  }

	render() {
		let period = this.state.period;
		let periodName = period.name;
		let periodStart = (new Date(period.created_at)).toDateString();
		let periodEnd = period.end ? (new Date(period.end)).toDateString() : 'Still running';
		return <div className="container-fluid">
			<div className="row">
				<div className="col-sm-6">
					<div className="card h-100">
			      <div className="card-body">
			      	<div className="row">
				      	<div className="col-4">
									<div style={{height: '100px', width: '100px', backgroundColor: 'green', borderRadius: '50%'}}></div>
								</div>
								<div className="col-8">
					        <h5 className="card-title font-weight-light">Current Period</h5>
					        {period && <div>
						        <p className="card-text font-weight-bold" style={{fontSize: '2rem'}}>{periodName}</p>
						        <p className="card-text"><span className="font-weight-light font-italic">Started:</span> <span className="badge badge-info">{periodStart}</span></p>
						        <p className="card-text"><span className="font-weight-light font-italic">Ended:</span> <span className="badge badge-dark">{periodEnd}</span></p>
					      	</div>}
					      	<div>
					      	{!period && <div>
					      			<p className="card-text font-weight-bold" style={{fontSize: '2rem'}}>No Active Period</p>
					      		</div>
					      	}
					      	</div>
					      </div>
					    </div>
			      </div>
			    </div>
				</div>
				<div className="col-sm-6">
					{!period.end && <h3>Runtime</h3>}
					{period.end && <h3>Lasted for</h3>}
					<div>
						<CountDown start={period.created_at} end={period.end} />
					</div>
					<div>
						{period && !period.end && <button onClick={this.closePeriod} className="btn btn-danger">Close Period</button>}
            {(!period || period.end) && <Link to={`${this.props.match.url}/new`} className="btn btn-success">Start New Period</Link>}
					</div>
				</div>
			</div>

			<br />
			<br />

      <DataTable ref={this.dataTable} title="Periods" config={this.dataTableConfig} >
        <th>Name</th>
        <th>Started</th>
        <th>Ended</th>
        <th className="disabled-sorting noExport">Action</th>
      </DataTable>

		</div>
	}
}

export class NewPeriod extends Component {
  state = { error: {} };
  submit = (data) => {
    return axios.post('/api/setup/periods', data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
      this.setState({error: error.response.data});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/periods');
  }
  render() {
    return <PeriodForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

export class EditPeriod extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
  if(this.props.location.state && this.props.location.state.student){
    this.setState({data: this.props.location.state.student})
  }else if(this.props.match.params.id){
    axios.get(`/api/setup/periods/${this.props.match.params.id}`).then(response => {
      this.setState({data: response.data});
    },error=>this.setState({error:error.response.data}))
  }
  
  }

  submit = (data) => {
    return axios.put(`/api/setup/periods/${this.props.match.params.id}`, data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
      this.setState({error: error.response.data});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/periods');
  }

  render() {
    return <PeriodForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
  }
}
