import React, { Component } from 'react';
import lodash from 'lodash';
import {inject} from 'mobx-react';

import ShowNotification from '../components/ShowNotification';
import confirmAction from '../utils/confirmAction';

@inject('teacherStore')
@inject('subjectStore')
export default class TeacherForm extends Component {
	defaults = {
		name: '',
		shorthand: '',
		teacher_id: '',
		subjects: []
	};

	state = {error: {}, loading: false, teachers: null, subjects: null, data: {...this.defaults}};

	componentDidMount() {
    this.props.teacherStore.list({}).then(teachers=>{
    	this.setState({teachers});
    });

    this.props.subjectStore.list({}).then(subjects=>{
    	this.setState({subjects});
    })
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (event) => {
		let field = event.target.name
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}
	
	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Stream Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Stream Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	setSubjectTeacher = (event) => {
		let data = this.state.data;
		let subjectId = event.target.name;
		let index = data.subjects.findIndex( sub => sub.subject_id == subjectId );

		if(index != -1) {
			data.subjects[index]['teacher_id'] = event.target.value;
		}else {
			data.subjects.push({teacher_id: event.target.value, subject_id: subjectId});
		}
		this.setState({data})
	}

	subjectTeacher = (subjectId) => {
		let sub = this.state.data.subjects.find( sub => sub.subject_id == subjectId );
		return sub ? sub.teacher_id : '';
	}

	delete = () => {
		confirmAction( () => {
			return axios.delete(`/api/setup/streams/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Stream was deleted'};
			}, error => {
				throw {error: "Stream couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Stream', canceled: "Deleting Stream Operation Cancelled", consequence: 'Stream information will be lost'});
	}

	close = () => {
		confirmAction( () => {
			return axios.post(`/api/setup/streams/close/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Closed!', succes_text: 'Stream was closed'};
			}, error => {
				throw {error: "Stream couldn't be closed"};
			})
		}, {loading_title: 'Closing...', loading_text: 'Closing Stream', canceled: "Closing Stream Operation Cancelled", consequence: 'Stream information will be archieved'});
	}

	restore = () => {
		confirmAction( () => {
      return axios.post(`/api/setup/streams/restore/${this.props.data.id}`).then(res=>{
        this.props.close();
        return {success: 'Restored!', succes_text: 'Stream was restored'};
      }, error => {
        throw {error: 'Stream could not be restored.'};
      })
    }, {loading_title: 'Restoring...', loading_text: 'Restoring Stream', canceled: "Restoring Stream Operation Cancelled", consequence: 'Stream information will be made accessible again'});
	}

	render() {
		let error = this.state.error || {};
		let nameInvalid='', shorthandInvalid='', teacherInvalid='';
		if(error.errors) {
			nameInvalid = error.errors.name ? 'is-invalid' : '';
			shorthandInvalid = error.errors.shorthand ? 'is-invalid' : '';
			teacherInvalid = error.errors.teacher_id ? 'is-invalid' : '';
		}

		let {subjects, teachers} = this.state;

		return <div className="container-fluid" >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">
					    	Stream Details
					    	{ this.props.edit && <div className="pull-right">
					  			{this.state.data.close ? <button type="button" disabled={this.state.loading} className="btn btn-secondary" onClick={this.restore} >Restore</button>
						 				: <button type="button" disabled={this.state.loading} className="btn btn-warning" onClick={this.close} >Close</button> }
						 			&nbsp;
					  			<button type="button" disabled={this.state.loading} className="btn btn-danger" onClick={this.delete} >Delete</button>
					    		</div>
					    	}
					    </h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
						      <div className="form-group col-sm-6">
						        <label>Name</label>
						        <input value={this.state.data.name} onChange={this.inputChanged} name="name" type="text" placeholder="Stream Name" className={`form-control ${nameInvalid}`} />
						        {nameInvalid && error.errors.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-sm-6">
						        <label>Shorthand</label>
						        <input value={this.state.data.shorthand} onChange={this.inputChanged} name="shorthand" type="text" placeholder="Stream Shorthand" className={`form-control ${shorthandInvalid}`} />
						        {shorthandInvalid && error.errors.shorthand.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					      <div className="form-group">
					        <label>Stream Teacher</label>
					        <select value={this.state.data.teacher_id || ''} onChange={this.inputChanged} name="teacher_id" className={`form-control ${teacherInvalid}`} >
					        	<option value="">Select Teacher</option>
                    {teachers && teachers.map( teacher => <option key={teacher.id} value={teacher.id}>{teacher.firstname} {teacher.lastname}</option>)}
                  </select>
					        {teacherInvalid && error.errors.teacher.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div>
					      	<label>Subjects Done</label>
					      	{subjects && subjects.map( subject => <div key={subject.id} className="form-group row">
			              <label className="col-sm-2 col-form-label">{subject.name}</label>
			              <div className="col-10">
			                <select value={this.subjectTeacher(subject.id)} onChange={this.setSubjectTeacher} name={subject.id} className={`form-control`} >
							        	<option value="">Select Teacher</option>
		                    {teachers && teachers.map( teacher => <option key={teacher.id} value={teacher.id}>{teacher.firstname} {teacher.lastname}</option>)}
		                  </select>	
			              </div>
			            </div>)}
		            </div>
					      

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}