import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {inject} from 'mobx-react';

import ClassForm from '../components/ClassForm';
import StreamForm from '../components/StreamForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('classesStore')
export default class Classes extends Component {
  constructor(props) {
    super(props);
    window.studentListActionEdit = this.edit;
    const self = this;
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax: function(params, callback, settings) {
        self.props.classesStore.list(params, true).then(classes=>{
          callback({data: classes});
        })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: 'start', name: 'start', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
              <a onclick="studentListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
          </span>`
        }}
      ],
      createdRow: function (row, data, index) {
        if(data.close) {
          row.classList.add('table-danger');
        }
      }
    };
  }

  edit = (cls) => {
    this.props.history.push(`${this.props.match.url}/${cls}`);
  }

	render() {
		let match = this.props.match;
		
		return <div className="container-fluid">
		  <div className="row">
		    <div className="col-md-12">
		      <span className="pull-right">
		        <Link className="btn btn-success details" to={`${match.url}/new`}>New Class</Link>
		      </span>
		    </div>
		  </div>
      <DataTable ref={this.dataTable} title="Classes" config={this.dataTableConfig}>
        <th>Name</th>
        <th>Start</th>
        <th className="disabled-sorting text-center noExport">Actions</th>
      </DataTable>
		</div>

	}
}

@inject('classesStore')
export class NewClass extends Component {
  state = { error: {} };
  submit = (data) => {
    return this.props.classesStore.create(data).then( cls => {
      this.setState({error: {}, data: cls})
      return cls;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/classes');
  }
  render() {
    return <ClassForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

@inject('classesStore')
export class EditClass extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.cls){
      this.setState({data: this.props.location.state.cls})
    }else if(this.props.match.params.classId){
      this.props.classesStore.get(this.props.match.params.classId).then(cls => {
        this.setState({data: cls});
      },error=>this.setState({error}))
    }
  }

  submit = (data) => {
    console.log(data);
    return this.props.classesStore.update(data).then( cls => {
      this.setState({error: {}, data: cls})
      return cls;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  delete = () => {
    return this.props.classesStore.delete(this.props.match.params.classId);
  }

  close = () => {
    this.props.history.push('/school/setup/classes');
  }

  render() {
    return <ClassForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} close={this.close} />
  }
}

export class StreamNew extends Component {
  state = { error: {} };
  submit = (data) => {
    return axios.post(`/api/setup/classes/${this.props.match.params.classId}/streams`, data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
      this.setState({error: error.response.data});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push(`/school/setup/classes/${this.props.match.params.classId}`);
  }
  render() {
    return <StreamForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

export class StreamEdit extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.stream){
      this.setState({data: this.props.location.state.stream})
    }else if(this.props.match.params.streamId){
      axios.get(`/api/setup/classes/${this.props.match.params.classId}/streams/${this.props.match.params.streamId}`).then(response => {
        this.setState({data: response.data.data});
      },error=>this.setState({error:error.response.data}))
    }
  }

  submit = (data) => {
    return axios.put(`/api/setup/classes/${this.props.match.params.classId}/streams/${this.props.match.params.streamId}`, data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
      this.setState({error: error.response.data});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push(`/school/setup/classes/${this.props.match.params.classId}`);
  }

  render() {
    return <StreamForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
  }
}