import React, {Component} from 'react';
import Countdown from './countDown';

import './style.css';

export default class _CountDown extends Component {
	el = React.createRef();
	cd = null;

	componentDidMount() {
		this.init();
	}

	componentDidUpdate(prevProps,prevState) {
		if(prevProps.start != this.props.start) {
			this.cd.stop();
			this.init();
		}
	}

	init() {
		let date = (new Date(this.props.start)).getTime();

		//if end props is passed, set date to the duration between start and end
		if(this.props.end){
			let end = (new Date(this.props.end)).getTime();
			date = Date.now() - (end - date);
		}
		this.cd = new Countdown({
	      cont: this.el.current,
	      countdown: false,
	      date: date ,
	      outputTranslation: {
	          year: 'Years',
	          week: 'Weeks',
	          day: 'Days',
	          hour: 'Hours',
	          minute: 'Minutes',
	          second: 'Seconds',
	      },
	      endCallback: null,
	      outputFormat: 'week|day|hour|minute|second',
	  });
	  this.cd.start();

	  //if end props is passed stop the count up
	  if(this.props.end){
	  	this.cd.stop();
	  }
	}

	render() {
		return <div ref={this.el} />;
	}
}
