import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {inject} from 'mobx-react';

import SubjectGroupForm from '../components/SubjectGroupForm';
import DataTable from '../components/DataTable';

import getCookie from '../utils/getCookie';

@inject('subjectGroupStore')
export default class SubjectGroup extends Component {
  constructor(props) {
    super(props);
    window.subjectGroupListActionEdit = this.edit;
    const self = this;
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax: function(params, callback, settings) {
        self.props.subjectGroupStore.list(params).then(subjectGroups => {
          callback({data: subjectGroups});
        })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
                  <a onclick="subjectGroupListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
              </span>`
        }}
      ],
    };
  }
  edit = (subject) => {
    this.props.history.push(`${this.props.match.url}/${subject}`);
  }

	render() {
		let match = this.props.match;
		
		return <div className="container-fluid">
		  <div className="row">
		    <div className="col-md-12">
		      <span className="pull-right">
		        <Link className="btn btn-success details" to={`${match.url}/new`}>New Subject Group</Link>
		      </span>
		    </div>
		  </div>

      <DataTable ref={this.dataTable} title="Subject Groups" config={this.dataTableConfig}>
        <th>Name</th>
        <th className="disabled-sorting text-center noExport">Actions</th>
      </DataTable>

		</div>

	}
}

@inject('subjectGroupStore')
export class SubjectGroupNew extends Component {
  state = { error: {} };
  submit = (data) => {
    return this.props.subjectGroupStore.create(data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/subject-groups');
  }
  render() {
    return <SubjectGroupForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

@inject('subjectGroupStore')
export class SubjectGroupEdit extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.subjectGroupId){
      this.setState({data: this.props.location.state.subjectGroupId})
    }else if(this.props.match.params.subjectGroupId){
      this.props.subjectGroupStore.get(this.props.match.params.subjectGroupId).then(subjectGroup => {
        this.setState({data: subjectGroup});
      },error=>this.setState({error}))
    }
  }

  submit = (data) => {
    return this.props.subjectGroupStore.update(data).then( subjectGroup => {
      this.setState({error: {}, data: subjectGroup})
      return subjectGroup;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/subject-groups');
  }

  render() {
    return <SubjectGroupForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
  }
}
