import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';

import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

class StreamList extends Component {
    constructor(props) {
        super(props);
        window.streamListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            data: this.props.streams,
            columns: [
                { data: 'name', name: 'name', 'class': 'text-center'},
                { data: 'shorthand', name: 'shorthand', 'class': 'text-center'},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="streamListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                    </span>`
                }}
            ],
            createdRow: function (row, data, index) {
                if(data.close) {
                    row.classList.add('table-danger');
                }
            }
        };
    }

    edit = (stream) => {
        this.props.history.push(`${this.props.match.url}/streams/${stream}`);
    }

	render() {
		let match = this.props.match;
		return <div>
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/streams/new`} className="btn btn-success details">Add Stream</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Streams" config={this.dataTableConfig}>
                <th>Name</th>
                <th>Shorthand</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export default withRouter(StreamList);
