import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import Period, {NewPeriod, EditPeriod} from './Period';
import Classes, {NewClass, EditClass, StreamNew, StreamEdit} from './Classes';
import Subject, {NewSubject,EditSubject} from './Subject';
import SubjectGroup, {SubjectGroupNew, SubjectGroupEdit} from './SubjectGroup';
import Grade from './Grade';
import Level from './Level';

export default class SetupPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route path={`${match.path}/periods/new`} render={(props) => <NewPeriod {...props} />} />
						<Route path={`${match.path}/periods/:id`} render={(props) => <EditPeriod {...props} />} />
						<Route path={`${match.path}/periods/`} render={(props) => <Period {...props} />} />

						<Route path={`${match.path}/classes/new`} render={(props)=> <NewClass {...props} /> } />
						<Route path={`${match.path}/classes/:classId/streams/new`} render={(props)=> <StreamNew {...props} /> } />
						<Route path={`${match.path}/classes/:classId/streams/:streamId`} render={(props)=> <StreamEdit {...props} /> } />
						<Route path={`${match.path}/classes/:classId`} render={(props)=> <EditClass {...props} /> } />
						<Route path={`${match.path}/classes`} render={(props)=> <Classes {...props} /> } />

						<Route path={`${match.path}/subject-groups/new`} render={(props)=> <SubjectGroupNew {...props} /> } />
						<Route path={`${match.path}/subject-groups/:subjectGroupId`} render={(props)=> <SubjectGroupEdit {...props} /> } />
						<Route path={`${match.path}/subject-groups`} render={(props)=> <SubjectGroup {...props} /> } />

						<Route path={`${match.path}/subjects/new`} render={(props)=> <NewSubject {...props} /> } />
						<Route path={`${match.path}/subjects/:id`} render={(props)=> <EditSubject {...props} /> } />
						<Route path={`${match.path}/subjects`} render={(props)=> <Subject {...props} /> } />

						<Route path={`${match.path}/grades`} render={(props)=> <Grade {...props} /> } />
						<Route path={`${match.path}/levels`} render={(props)=> <Level {...props} /> } />
						<Route path=""><h1>404 Not Found</h1></Route>
					</AnimatedSwitch>
	}
}