import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {inject} from 'mobx-react';

import SubjectForm from '../components/SubjectForm';
import DataTable from '../components/DataTable';

import getCookie from '../utils/getCookie';

@inject('subjectStore')
export default class Subject extends Component {
  constructor(props) {
    super(props);
    window.studentListActionEdit = this.edit;
    const self = this;
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax: function(params, callback, settings) {
        self.props.subjectStore.list(params).then(subjects=>{
          callback({data: subjects});
        })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: 'shorthand', name: 'shorthand', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
                  <a onclick="studentListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
              </span>`
        }}
      ],
    };
  }
  edit = (subject) => {
    this.props.history.push(`${this.props.match.url}/${subject}`);
  }

	render() {
		let match = this.props.match;
		
		return <div className="container-fluid">
		  <div className="row">
		    <div className="col-md-12">
		      <span className="pull-right">
		        <Link className="btn btn-success details" to={`${match.url}/new`}>New Subject</Link>
		      </span>
		    </div>
		  </div>

      <DataTable ref={this.dataTable} title="Subjects" config={this.dataTableConfig}>
        <th>Name</th>
        <th>Shorthand</th>
        <th className="disabled-sorting text-center noExport">Actions</th>
      </DataTable>

		</div>

	}
}

@inject('subjectStore')
export class NewSubject extends Component {
  state = { error: {} };
  submit = (data) => {
    return this.props.subjectStore.create(data).then( subject => {
      this.setState({error: {}, data: subject})
      return subject;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/subjects');
  }
  render() {
    return <SubjectForm data={this.state.data} error={this.state.error} submit={this.submit} close={this.close} />
  }
}

@inject('subjectStore')
@inject('subjectGroupStore')
export class EditSubject extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.subject){
      this.setState({data: this.props.location.state.subject})
    }else if(this.props.match.params.id){
      this.props.subjectStore(this.props.match.params.id).then(subject => {
        this.setState({data: subject});
      },error=>this.setState({error}))
    }
  }

  submit = (data) => {
    return this.props.subjectStore.update(data).then( subject => {
      this.setState({error: {}, data: subject})
      return subject;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/setup/subjects');
  }

  render() {
    return <SubjectForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
  }
}
