import React, {Component} from 'react';
import { observable, set } from 'mobx'
import { observer } from "mobx-react";
import lodash from 'lodash';

import confirmAction from '../utils/confirmAction';

@observer
export default class Level extends Component {
	@observable levels = []
	state = {loading: false, name: '', shorthand: '', error: {}};
	componentDidMount() {
		axios.get('/api/setup/levels').then(res=>{
			set(this.levels, res.data);
		})
	}

	inputChanged(field, event) {
		this.setState({[field]: event.target.value});
	}

	addLevel = () => {
		let data = {
			name: this.state.name,
			shorthand: this.state.shorthand
		};
		this.setState({loading: true});
		axios.post('/api/setup/levels', data).then(res=>{
			this.levels.push(res.data);
			this.setState({name: '', shorthand: '', error: {}});
		},error => {
			this.setState({error: error.response.data});
		}).finally( ()=>this.setState({loading: false}) );
	}

	render() {
		let levels = this.levels;
		let parentLevels = levels.filter( l => l.level_id == null ).sort( (a,b) => a.pos > b.pos );

		let error = this.state.error.errors || {};
		let nameInvalid = error.name ? 'is-invalid' : '';
		let shorthandInvalid = error.shorthand ? 'is-invalid' : '';

		return <div className="container-fluid">
			<div className="row">

				<div className="col-12">
					{parentLevels.map( level => <LevelView key={level.id} level={level} levels={levels} /> )}
				</div>

				<div className="col-12">
					<div className="card">
		        <div className="card-body">
		        	<div className="row">
		        		<div className="form-group col-5">
					        <input type="text" value={this.state.name} onChange={this.inputChanged.bind(this,'name')} className={`form-control ${nameInvalid}`} placeholder="Name" />
					        {error.name && error.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>
		        		<div className="form-group col-5">
					        <input type="text" value={this.state.shorthand} onChange={this.inputChanged.bind(this,'shorthand')} className={`form-control ${shorthandInvalid}`} placeholder="Shorthand" />
					        {error.shorthand && error.shorthand.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>
		        		<div className="col-2">
		        			<input type="button" onClick={this.addLevel} disabled={this.state.loading} className="btn btn-primary" value="Add Level" />
		        		</div>
		        	</div>
		        </div>
		      </div>
				</div>

			</div>
		</div>
	}
}

@observer
class LevelView extends Component {
	constructor(props) {
		super(props);
		this.state = {loading: false, name: '', shorhand: '', newName: '', newShorthand: '', activeLevel: null, error: {}};
		if(this.props.level){
			this.state.name = this.props.level.name;
			this.state.shorthand = this.props.level.shorthand;
		}
	}
	componentDidMount() {
		this.setActiveLevel(this.props.level);
	}
	componentDidUpdate(prevProps,prevState) {
		if(!lodash.isEqual(this.props.level, prevProps.level)) {
			this.setActiveLevel(this.props.level);
		}
	}

	inputChanged(field, event) {
		this.setState({[field]: event.target.value});
	}

	setActiveLevel(level) {
		if(!level)
			return;

		let levelChain = [level];
		let iLevel = level;
		do{
			iLevel = this.props.levels.find( l => l.id == iLevel.level_id);
			if(iLevel){
				levelChain.push(iLevel);
			}
		}while(iLevel && iLevel.level_id);
		levelChain.reverse();
		this.setState({levelChain, activeLevel: level, name: level.name, shorthand: level.shorthand});
	}

	editLevel = () => {
		let data = {
			name: this.state.name,
			shorthand: this.state.shorthand
		};
		this.setState({loading: true});
		axios.put(`/api/setup/levels/${this.state.activeLevel.id}`, data).then(res=>{
			let index = this.props.levels.findIndex( item => item.id == this.state.activeLevel.id );
			this.props.levels[index] = res.data;
			this.setState({newName: '', newShorthand: '', error: {}});
		},error => {
			this.setState({error: error.response.data});
		}).finally( ()=>this.setState({loading: false}) );
	}

	addSubLevel = () => {
		let data = {
			name: this.state.newName,
			shorthand: this.state.newShorthand,
			level_id: this.state.activeLevel.id
		};
		this.setState({loading: true});
		axios.post('/api/setup/levels', data).then(res=>{
			this.props.levels.push(res.data);
			this.setState({newName: '', newShorthand: '', error: {}});
		},error => {
			this.setState({error: error.response.data});
		}).finally( ()=>this.setState({loading: false}) );
	}

	delete = () => {
		confirmAction( () => {
			return axios.delete(`/api/setup/levels/${this.state.activeLevel.id}`).then(res=>{
				let parent = this.props.levels[this.props.levels.findIndex(l => l.id == this.state.activeLevel.level_id)];
				let index = this.props.levels.findIndex( l => l.id == this.state.activeLevel.id );
				this.props.levels.splice(index, 1);
				this.setActiveLevel(parent);
				return {success: 'Deleted!', succes_text: 'Level was deleted'};
			}, error => {
				throw {error: "Level couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Level', canceled: "Deleting Level Operation Cancelled", consequence: 'Level information will be lost'});
	}

	render() {
		let level = this.state.activeLevel;
		let subLevels = this.props.levels.filter( l => l.level_id == (level && level.id) ).sort( (a,b) => a.pos > b.pos );
		let levelChain = this.state.levelChain || [];

		let error = this.state.error.errors || {};
		let nameInvalid = error.name ? 'is-invalid' : '';
		let shorthandInvalid = error.shorthand ? 'is-invalid' : '';

		return <div className="card">
      <div className="card-header">
        <div>
        	<nav aria-label="breadcrumb">
          	<ol className="breadcrumb">
          		{levelChain.map( item => <li key={item.id} className="breadcrumb-item active" aria-current="page">
          		  <button type="button" onClick={this.setActiveLevel.bind(this,item)} className="btn btn-secondary">
          				{item.name} <span className="badge badge-success">{item.shorthand}</span>
          			</button>
          		</li> )}
          	</ol>
        	</nav>
        </div>
      </div>
      <div className="card-body">
      	<div className="row">
      		<div className="col-8">
	        	<div className="list-group">
	        		{subLevels.map( subLevel => <button key={subLevel.id} type="button"
	        			onClick={this.setActiveLevel.bind(this, subLevel)}
	        			className="list-group-item list-group-item-action d-flex justify-content-between align-items-center">
	        			{subLevel.name} <span className="badge badge-info">{subLevel.shorthand}</span>
	        		</button> )}
	        	</div>

	        	<div className="row mt-3 justify-content-center">
	        		<div className="form-group col-4">
				        <input type="text" value={this.state.newName} onChange={this.inputChanged.bind(this,'newName')} className={`form-control ${nameInvalid}`} placeholder="Name" />
				        {error.name && error.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
								<p className="help-block help-block-error"></p>
				      </div>
				      <div className="form-group col-4">
				        <input type="text" value={this.state.newShorthand} onChange={this.inputChanged.bind(this,'newShorthand')} className={`form-control ${shorthandInvalid}`} placeholder="Shorthand" />
				        {error.shorthand && error.shorthand.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
								<p className="help-block help-block-error"></p>
				      </div>
		      		<div className="col-2">
		      			<input type="button" onClick={this.addSubLevel} disabled={this.state.loading} className="btn btn-primary" value="Add Sub Level" />
		      		</div>
		      	</div>

      		</div>
      		<div className="col-4">
      			<div className="col-12 border border-secondary rounded">
        			<div className="mb-2 mt-2">
        				<input type="text" className="form-control" value={this.state.name} onChange={this.inputChanged.bind(this,'name')} placeholder="Name" />
        			</div>
        			<div className="mb-2">
        				<input type="text" className="form-control" value={this.state.shorthand} onChange={this.inputChanged.bind(this, 'shorthand')} placeholder="Shorhand" />
        			</div>
        			<div className="mb-2">
        				<div className="row">
	        				<div className="col">
	        					<button type="button" onClick={this.editLevel} disabled={this.state.loading} className="btn btn-success btn-sm btn-block">Edit</button>
	        				</div>
	        				<div className="col">
	        					<button onClick={this.delete} type="button" className="btn btn-danger btn-sm btn-block">Delete</button>
	        				</div>
        				</div>
        			</div>
      			</div>
      		</div>
      	</div>
      </div>
    </div>
	}
}