import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from './ShowNotification';
import DateInput from './DateInput';
import StreamList from './StreamList';
import confirmAction from '../utils/confirmAction';

export default class ExamForm extends Component {
	defaults = {
		name: ''
	};

	state = { error: {}, loading: false, data: {...this.defaults}, levels: [], levelChain: [], subLevelChains: {}};

	componentDidMount() {
		axios.get('/api/setup/levels').then(res=>{
			this.setState({levels: res.data});
		})
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}

		//check for changes in level
		if(this.props.data && this.props.data['level_id'] != prevProps.data['level_id']) {
			let level = this.state.levels.find( l => l.id == this.props.data['level_id']);
			this.setLevel(level);
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Class Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Class Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error});
			if(error && typeof(error) == typeof('')){
				ShowNotification(error.response.data.error, "danger", "nc-simple-remove");
				return;
			}
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	setLevel = (level) => {
		let levelChain = [];
		let subLevelChains = {};
		if(level) {
			levelChain = [level];
			let iLevel = level;
			do{
				iLevel = this.state.levels.find( l => l.id == iLevel.level_id);
				if(iLevel){
					levelChain.push(iLevel);
				}
			}while(iLevel && iLevel.level_id);
		}
		levelChain.reverse();
		for(var i in levelChain) {
			subLevelChains[levelChain[i].id] =  this.state.levels.filter( l => l.level_id == levelChain[i].id);
		}

		this.setState( (state) => {
			state.levelChain = levelChain;
			state.subLevelChains = subLevelChains;
			state.data['level_id'] = level.id;
			return state;
		});
	}

	delete = () => {
		confirmAction( () => {
			return this.props.delete().then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Class was deleted'};
			}, error => {
				throw {error: "Class couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Class', canceled: "Deleting Class Operation Cancelled", consequence: 'Class information will be lost'});
	}

	close = () => {
		confirmAction( () => {
			return axios.post(`/api/setup/classes/close/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Closed!', succes_text: 'Class was closed'};
			}, error => {
				throw {error: "Class couldn't be closed"};
			})
		}, {loading_title: 'Closing...', loading_text: 'Closing Class', canceled: "Closing Class Operation Cancelled", consequence: 'Class information will be archieved'});
	}

	restore = () => {
		confirmAction( () => {
      return axios.post(`/api/setup/classes/restore/${this.props.data.id}`).then(res=>{
        this.props.close();
        return {success: 'Restored!', succes_text: 'Class was restored'};
      }, error => {
        throw {error: 'Class could not be restored.'};
      })
    }, {loading_title: 'Restoring...', loading_text: 'Restoring Class', canceled: "Restoring Class Operation Cancelled", consequence: 'Class information will be made accessible again'});
	}

	render() {
		let { classes, subjects } = this.state;

		let error = this.state.error || {};
		let nameInvalid='', startInvalid='';
		if(error.errors) {
			nameInvalid = error.errors.name ? 'is-invalid' : '';
			startInvalid = error.errors.start ? 'is-invalid' : '';
		}

		let levels = this.state.levels;
		let subLevelChains = this.state.subLevelChains;
		let baseLevels = levels.filter( item => item.level_id == null);

		return <div className="container-fluid" ref={this.elRef} >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">
					    	Class Details
					    	{ this.props.edit && <div className="pull-right">
					    			{this.state.data.close ? <button type="button" disabled={this.state.loading} className="btn btn-secondary" onClick={this.restore} >Restore</button>
    	      	 				: <button type="button" disabled={this.state.loading} className="btn btn-warning" onClick={this.close} >Close</button> }
    	      	 			&nbsp;
					    			<button type="button" disabled={this.state.loading} className="btn btn-danger" onClick={this.delete} >Delete</button>
					    		</div>
					    	}
					    </h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
						      <div className="form-group col-sm-6">
						        <label>Class Name</label>
						        <input value={this.state.data.name} onChange={this.inputChanged.bind(this,'name')} type="text" placeholder="Class Name" className={`form-control ${nameInvalid}`} />
						        {nameInvalid && error.errors.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-sm-6">
						        <label>Start Date</label>
						        <DateInput value={this.state.data.start} onChange={this.inputChanged.bind(this,'start')} placeholder="Start Date" className={`form-control ${startInvalid}`} />
						        {startInvalid && error.errors.start.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					      <div className="row">
					      	<div className="col-12">

					      		<ul className="list-group list-group-horizontal-sm">
					      			<li className="list-group-item">
					      				<div className="btn-group dropright">
							      			<button type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
							      				Set Level
							      			</button>
							      			<div className="dropdown-menu">
							      				{baseLevels.map( (level) => <a key={level.id} onClick={this.setLevel.bind(this,level)} className="dropdown-item">{level.name}</a> )}
							      			</div>
							      		</div>
					      			</li>
					      			{this.state.levelChain.map( level => <li key={level.id} className="list-group-item">
					      				<div className="btn-group dropright">
							      			<button type="button" className={`btn btn-secondary ${subLevelChains[level.id].length ? 'dropdown-toggle' : ''}`} data-toggle={`${subLevelChains[level.id].length ? 'dropdown' : ''}`} aria-haspopup="true" aria-expanded="false">
							      				{level.name}
							      			</button>
							      			{subLevelChains[level.id].length != 0 && <div className="dropdown-menu">
							      				{subLevelChains[level.id].map( subLevel => <a key={subLevel.id} onClick={this.setLevel.bind(this,subLevel)} className="dropdown-item">{subLevel.name}</a> )}
							      			</div>}
							      		</div>
					      			</li>
					      			)}
					      		</ul>

					      	</div>
					      </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>

					</div>
		    </div>
		  </div>

		  { this.state.data.streams ? <StreamList classId={this.state.data.id} streams={this.state.data.streams} /> : <div/> }

		</div>

	}
}
