import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class Grade extends Component {
  constructor(props) {
    super(props);
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      ordering: false,
      paging: false,
      searching: false,
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        axios.get('/api/setup/grades', {params: {params}}).then(response=>{
          callback(response.data);
        })
      },
      columns: [
        { data: 'symbol', name: 'symbol', 'class': 'text-center'},
        { data: 'min', name: 'min', 'class': 'text-center', render: (data,type,row) => row.min ? row.min : '-'},
        { data: 'max', name: 'max', 'class': 'text-center', render: (data,type,row) => row.max ? row.max : '-'}
      ],
      dom: 'lfrtip'
    }
  }
  componentDidMount() {
    this.dataTable.current.table.MakeCellsEditable({
      onUpdate: this.updateValue,
      columns: [1,2]
    });

  }

  updateValue = (updatedCell, updatedRow, oldValue) => {
    let min = updatedRow.data()['min'];
    let max = updatedRow.data()['max'];
    let grade = updatedRow.data()['symbol'];
    return axios.post('/api/setup/grades', {min, max, symbol: grade});
  }

  render() {
    let match = this.props.match;
    
    return <div className="container-fluid">
      <DataTable ref={this.dataTable} title="Grades" config={this.dataTableConfig}>
        <th>Grade</th>
        <th>Min</th>
        <th>Max</th>
      </DataTable>
    </div>

  }
}
